import styled from "styled-components"
import backgroundSrc from './assets/backgroundNoBlur.png'
import { animated } from '@react-spring/web'

export default styled(animated.div)`
    display: flex;
    justify-content: center;
    align-items: flex-end;
    z-index: 5;
    position: relative;

    height: calc(100vh - 5rem);
    width: 100%;
    max-width: 900px;
    min-width: 900px;
    max-height: calc(100% - 16px);
    transform-origin: 50% 100%;

    &::after {
        position: absolute;
        content: ' ';
        background-image: url(${backgroundSrc});
        bottom: 0;
        right: 0;
        left: 0;
        height: 1070px;
        background-size: 100% auto;
        background-position: bottom left;
        background-repeat: no-repeat;
        z-index: 15;

    }
    @media (max-height: 920px) or (max-width: 920px)  {
        margin-bottom: -6rem;

    }

    @media (max-width: 1260px) or (max-height: 1240px) {
        transform: scale(1);
        height: 130vh;
    }

    @media (max-width: 740px) and (max-height: 960px) {

        max-height: 200%;
        height: 200%;
    }
  



    @media (max-width: 920px) or (max-height: 1160px) {
        transform: scale(0.9);

    }
    @media (max-width: 740px) or (max-height: 960px) {
        transform: scale(0.8);

    }
    @media (max-width: 390px) or (max-height: 800px) {
        transform: scale(0.7);
    }

`;
