import React from "react"
import { HugoStateApi } from "../hugo"
import useStartpageHugo from "./useStartpageHugo"
import { Cell, Icon, Paper, Row, Wall, mailIcon } from "../ui"
import plan from "./assets/plan.png"

type Props = {
    hugoApi: HugoStateApi
}

const Startpage: React.FC<Props> = ({ hugoApi }) => {
    useStartpageHugo(hugoApi)

    return (
        <Wall>
            <Paper>
                <Row skewStrength={3}>
                    <Cell width={2}>
                        <p>Hey, my name is Alex!</p>
                        <p>This is my application system test laboratory, feel free to interact with the exhibition pieces (mouse / keyboard)!</p>
                    </Cell>
                </Row>
                <Row skewStrength={3}>
                    <Cell width={2}>
                        <p>
                            If one of the systems works successfully and arranged a job offer, please contact me immediately!
                        </p>
                        <p><Icon src={mailIcon} /> <a href="mailto:alexander@muehrenberg"><strong>alexander@muehrenberg</strong></a></p>
                        <p>Phone: <a href="mailto:alexander@muehrenberg"><strong>01575 / 66 927 88</strong></a></p>
                        <p></p>
                    </Cell>
                </Row>
                <Row skewStrength={1}>
                    <Cell width={2} style={{ flexDirection: 'row', display: 'flex' }}>
                        <div style={{ marginRight: '5rem' }}>
                            <h3>Commands</h3>
                            <ul>
                                <li>"<strong>CV</strong>"</li>
                                <li>"<strong>contact</strong>"</li>
                                <li>"<strong>hobbies</strong>"</li>
                                <li>"<strong>languages</strong>"</li>
                                <li>"<strong>more</strong>"</li>
                            </ul>
                        </div>
                        <div>
                            <p>
                                P.S.: Don't <strong>test</strong> Hugo or <strong>hack</strong> him, he doesn't really like that.
                                Better ask for some <strong>music</strong>! =)
                            </p>
                        </div>
                    </Cell>
                </Row>
            </Paper>
            <Paper rotate={-3}>
                <Row height={1.9} skewStrength={0.5} hueRotate={100} contrast={100} saturate={170} brightness={95}>
                    <Cell width={1.5} style={{ textAlign: 'center' }}>
                        <h2 style={{ margin: 0 }}>Master Plan</h2>
                        <img src={plan} style={{ width: '100%', opacity: 0.8}}/>
                    </Cell>
                </Row>
            </Paper>
        </Wall>
    )
}

export default Startpage